.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 20vh; /* Reduce la altura mínima */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px; /* Añade relleno para espaciar el contenido */
}

.App-main {
  padding: 20px;
}

.App-footer {
  background-color: #282c34;
  color: white;
  padding: 10px;
  position: fixed;
  width: 100%;
  bottom: 0;
}

h1 {
  font-size: 2rem; /* Ajusta el tamaño de la fuente del encabezado */
  margin: 0;
}

button {
  background-color: #61dafb;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 10px 2px;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background-color: #21a1f1;
}

input[type="file"] {
  margin: 20px 0;
}

pre {
  background-color: #f4f4f4;
  padding: 10px;
  text-align: left;
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #ddd;
  margin-top: 20px; /* Añade un margen superior */
}

.error-box {
  background-color: #fdd;
  border: 1px solid #f99;
  padding: 10px;
  margin-top: 20px;
  text-align: left;
}

.error-box h2 {
  color: #d00;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

